import { Component, OnInit, NgZone } from '@angular/core';
import {CommentService} from '../comment.service';
import {ActivatedRoute} from '@angular/router';
import {Comment} from '../Comment';
import {HubConnection} from '@aspnet/signalr-client';
import {SignalRService} from '../Signalr.service';
import {Mycomment} from '../MyComment';

@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: ['./comment.component.css']
})
export class CommentComponent implements OnInit {
  public currentMessage: string;
  public allMessages: string;
  public canSendMessage: Boolean;
  comment: Comment[];
  messages: Mycomment[] = [];
  messagesPrint: string;
  _storyId: number;
  constructor(private route: ActivatedRoute,
              private commentService: CommentService,
              private _signalRService: SignalRService,
              private _ngZone: NgZone) {
    this.subscribeToEvents();
    this.canSendMessage = _signalRService.connectionExists;
    this._storyId = +this.route.snapshot.paramMap.get('id');

  }
  private subscribeToEvents(): void {
    this._signalRService.connectionEstablished.subscribe(() => {
      this.canSendMessage = true;
    });
    this._signalRService.messageReceived.subscribe((message: Mycomment) => {
      this._ngZone.run(() => {
        if ( message.StoryId === this._storyId) {
          this.comment.push(new Comment(message.Mes, message.StoryId))
          this.messagesPrint = '';
        }
      });
    });
    this._signalRService.KeyPress.subscribe((data: Mycomment) => {
      this._ngZone.run(() => {
        const test: string = '';
        if (data.StoryId === this._storyId) {
          this.messagesPrint = '';
          if (data.Mes !== test ) {
            this.messagesPrint = data.Name + ': ' + data.Mes;
          }
        }
      });
    });
  }


/*  private _hubConnection: HubConnection;
  public async: any;
  message = '';
  messages: string[] = [];
  public sendMessage(): void {
    const data = `Sent: ${this.message}`;

    this._hubConnection.invoke('sendToAll', 'nick' , data);
    this.messages.push(data);
  }*/

  ngOnInit() {

    /* this._hubConnection = new HubConnection('http://localhost:51835/comment');
     console.log(this._hubConnection);

     this._hubConnection.start()
       .then(() => {
         console.log('Hub connection started');
       })
       .catch(err => {
         console.log('Error while establishing connection');
       });
     this._hubConnection.on('sendToAll', (nick: string, data: any) => {
       const received = `${nick} : ${data}`;
       this.messages.push(received);
     });*/
    this.initalize();
  }
  initalize() {
    const id = +this.route.snapshot.paramMap.get('id');
    this.commentService.getComment(id)
      .subscribe((storyData) => this.comment = storyData);
  }
public SendKeyPress(event: any, name: string): void {
    const mes = event.target.value;
  this._signalRService.sendPrint(new Mycomment(name, mes, this._storyId) );
}
  public sendMessage(name: string, mess: string): void {
    this._signalRService.sendMessage(new Mycomment(name, mess, this._storyId));
    this.commentService.addComment(new Comment(mess, this._storyId) )
      .subscribe((x) => this.comment.push(x));
  }

}



