import { Component, OnInit} from '@angular/core';
import {Story} from '../Story';
import { ActivatedRoute } from '@angular/router';
import { Location } from '@angular/common';
import {StoryService} from '../story.service';

@Component({
  selector: 'app-story-detali',
  templateUrl: './story-detali.component.html',
  styleUrls: ['./story-detali.component.css']
})
export class StoryDetailComponent implements OnInit {
  story: Story;
  constructor(private route: ActivatedRoute,
              private storyService: StoryService,
              private location: Location) {}

getStory()
{
  const id = +this.route.snapshot.paramMap.get('id');
  this.storyService.getStoryById(id).subscribe(x => this.story = x);
}

  ngOnInit() {
    this.getStory();
  }
  goBack(): void {
    this.location.back();
  }

  save(): void {
    this.storyService.updateStory(this.story)
      .subscribe(x => this.getStory());
  }
}



