import {Injectable, EventEmitter} from '@angular/core';
import {Mycomment} from './MyComment';

declare const $: any;
@Injectable()
export class SignalRService {
  // Declare the variables
  private proxy: any;
  private proxyName = 'comment';
  private connection: any;
  // create the Event Emitter
  public messageReceived: EventEmitter < Mycomment > ;
  public KeyPress: EventEmitter < Mycomment > ;
  public connectionEstablished: EventEmitter < Boolean > ;
  public connectionExists: Boolean;
  constructor() {
    this.connectionEstablished = new EventEmitter < Boolean > ();
    this.messageReceived = new EventEmitter < Mycomment > ();
    this.KeyPress = new EventEmitter < Mycomment > ();
    this.connectionExists = false;
    // create hub connection
    this.connection = ($.hubConnection( 'http://localhost:51835'))
    // create new proxy as name already given in top
    this.proxy = this.connection.createHubProxy(this.proxyName);
    // register on server events
    this.registerOnServerEvents();
    // call the connecion start method to start the connection to send and receive events.
    this.startConnection();
  }
  // method to hit from client
  public sendMessage(data: Mycomment) {
    // server side hub method using proxy.invoke with method name pass as param
    this.proxy.invoke('sendToAll', data );
  }

  public sendPrint(data: Mycomment) {
    // server side hub method using proxy.invoke with method name pass as param
    this.proxy.invoke('whoPrint', data );
  }
  // check in the browser console for either signalr connected or not
  private startConnection(): void {
    this.connection.start().done((data: any) => {
      console.log('Now connected ' + data.transport.name + ', connection ID= ' + data.id);
      this.connectionEstablished.emit(true);
      this.connectionExists = true;
    }).fail((error: any) => {
      console.log('Could not connect ' + error);
      this.connectionEstablished.emit(false);
    });
  }
  private registerOnServerEvents(): void {
    this.proxy.on('invokeAsync', (data: Mycomment) => {
      console.log('received in SignalRService: ' + JSON.stringify(data));
      this.messageReceived.emit(data);
    });
    this.proxy.on('print', (data: Mycomment) => {
      console.log('Print: ' + JSON.stringify(data));
      this.KeyPress.emit(data);
    });
  }
}



