import { Component, OnInit } from '@angular/core';
import {Story} from '../Story';
import {StoryService} from '../story.service';
import {ActivatedRoute} from '@angular/router';
import {Location} from '@angular/common';

@Component({
  selector: 'app-stories',
  templateUrl: './stories.component.html',
  styleUrls: ['./stories.component.css']
})
export class StoriesComponent implements OnInit {
  story: Story[];
  constructor(private route: ActivatedRoute,
              private storyService: StoryService,
              ) {


  }
  delete(id: number) {
    this.storyService.deleteStory(id)
      .subscribe(x => this.story.splice( this.story.indexOf(this.story.find(s => s.id === x)) , 1 ));
  }
  add(theme: string, text: string): void {
    theme = theme.trim();
    text = text.trim();
    if (!theme || !text) {
      return;
    }
     this.storyService.addStory(new Story(theme, text) )
       .subscribe((x) => this.story.push(x));
  }
  ngOnInit() {
    this.initalize();
  }
  initalize() {
    this.storyService.getStory()
      .subscribe((storyData) => this.story = storyData);
  }
}



