import { Injectable } from '@angular/core';
import {Story} from './Story';
import {Observable} from 'rxjs/Observable';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Http, Response} from '@angular/http';
import 'rxjs/add/operator/map';


@Injectable()
export class StoryService {
   httpOptions = {
    headers: new HttpHeaders({ 'Content-Type': 'application/json' })
  };
  constructor(private http: HttpClient, private _http: Http) { }
  private Url = 'http://localhost:51835/api/Story';
  getStory(): Observable<Story[]> {
      var result =  this._http.get(this.Url);
    return result.map((responce: Response) => {
      return <Story[]>responce.json();
    });
}
  updateStory (story: Story): Observable<any> {
    return this.http.put(this.Url, story, this.httpOptions);
  }

  getStoryById(id: number): Observable<Story> {
    const result =  this._http.get(this.Url + '/' + id);
    return result.map((responce: Response) => {
      return <Story>responce.json();
      });
  }

  deleteStory (id: number):  Observable<number> {
    const url = `${this.Url}/${id}`;
    const result = this._http.delete(url);
    return result.map((responce: Response) => {
      return <number>responce.json();
  });
  }
  /** POST: add a new hero to the server */
  addStory (story: Story): Observable<Story> {
    const result = this._http.post(this.Url, story);
    return result.map((responce: Response) => {
      return <Story>responce.json();
    });
  }


}



